:root {
    --app-background-dark: rgb(24,23,32);
}
 body{
    background-color: var(--app-background-dark);
 }

 .container {
    width: min(100% - 30px, 1200px);
    margin-inline: auto;
 }

 main{
  height: 100svh;
  display: grid;
  place-items: center;
 
 div {
   text-align: center;
 }
}
 
  h1, h2, h3 ,p , li , a {
    color: #fff;
  }

  h2 {
    font-size:2.4rem;
  }

  p {
   margin-top: 15px;
    font-size: 1.8rem;

  }
 
  h2 {
   font-size:4rem;
   font-weight: 400;
   line-height: 1;

   span {
     color: #00f3b6;
   }

 }

 input {
  width: 200px;
  height: 44px;
  margin-top: 40px;
  padding: 0 15px;
  border-radius: 50px;
  border: 1px solid rgb(255 255 255 / 5%);
  background-color: 1px solid rgb(255 255 255 / 5%);
  font-size: 1.8rem;
  text-align: center;
 }
